'use client';

import React, { useState } from 'react';
import Icon from '@mdi/react';
import { 
  mdiArrowLeft, 
  mdiChevronDown,
  mdiClose
} from '@mdi/js';
import { colors } from '@/lib/design-tokens';
import CanaryButton from '@/components/canary-ui/buttons/CanaryButton';
import { ButtonType, ButtonSize, IconPosition } from '@/components/canary-ui/buttons/types';
import CanarySelect from '@/components/canary-ui/forms/CanarySelect';
import { CanarySelectOption } from '@/components/canary-ui/forms/CanarySelect';
import { InputSize } from '@/components/canary-ui/forms/types';

interface MobileMenuOrderingProps {
  onBack: () => void;
  menuName?: string;
  menus?: Array<{ name: string; entryPoint: string; }>;
}

interface MenuItem {
  id: string;
  name: string;
  description: string;
  price: number;
  image?: string;
}

interface MenuSection {
  id: string;
  name: string;
  items: MenuItem[];
}

export const MobileMenuOrdering: React.FC<MobileMenuOrderingProps> = ({ 
  onBack, 
  menuName = "Breakfast menu",
  menus = []
}) => {
  const [selectedMenuName, setSelectedMenuName] = useState(menuName);
  const [selectedSection, setSelectedSection] = useState('cold');
  
  // Convert menus to CanarySelect options format
  const menuOptions: CanarySelectOption[] = menus.map(menu => ({
    label: menu.name,
    value: menu.name
  }));

  // Sample menu sections - since item creation flow hasn't been built yet, we'll show empty sections
  const menuSections: MenuSection[] = [
    { id: 'cold', name: 'Cold', items: [] },
    { id: 'mains', name: 'Mains', items: [] },
    { id: 'beverages', name: 'Beverages', items: [] }
  ];

  const handleMenuSelect = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedMenuName(event.target.value);
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 p-8">
      {/* Back to Mobile Landing Button */}
      <div className="fixed top-8 left-8 z-50">
        <CanaryButton
          onClick={onBack}
          type={ButtonType.OUTLINED}
          size={ButtonSize.NORMAL}
          icon={<Icon path={mdiArrowLeft} size={0.8} />}
          iconPosition={IconPosition.LEFT}
        >
          Back to Mobile Landing
        </CanaryButton>
      </div>

      {/* Mobile Frame */}
      <div 
        className="relative bg-white overflow-hidden shadow-xl"
        style={{
          width: '430px',
          height: '932px',
          borderRadius: '44px',
          boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.16)'
        }}
      >
        {/* Navigation Header */}
        <div className="flex items-center justify-between px-4 py-3 bg-white border-b border-neutral-200">
          <CanaryButton 
            onClick={onBack}
            type={ButtonType.ICON_SECONDARY}
            size={ButtonSize.NORMAL}
            icon={<Icon path={mdiArrowLeft} size={1} />}
          />
          
          <h1 
            className="font-roboto text-[16px] font-semibold text-center"
            style={{ color: colors.black1 }}
          >
            In-room dining
          </h1>
          
          <div className="w-10 h-10" />
        </div>

        {/* Menu Dropdown */}
        <div className="px-4 py-4 border-b border-neutral-200">
          <div className="relative">
            <button
              onClick={() => setIsMenuDropdownOpen(!isMenuDropdownOpen)}
              className="w-full flex items-center justify-between p-3 border border-neutral-200 rounded-lg bg-white"
            >
              <span 
                className="font-roboto text-[14px] font-medium"
                style={{ color: colors.black1 }}
              >
                {selectedMenuName}
              </span>
              <Icon 
                path={mdiChevronDown} 
                size={0.8} 
                color={colors.black1}
                className={`transition-transform ${isMenuDropdownOpen ? 'rotate-180' : ''}`}
              />
            </button>

            {/* Dropdown Menu */}
            {isMenuDropdownOpen && (
              <div className="absolute top-full left-0 right-0 mt-2 bg-white border border-neutral-200 rounded-lg shadow-lg z-10">
                {menus.length > 0 ? (
                  menus.map((menu) => (
                    <button
                      key={menu.name}
                      onClick={() => handleMenuSelect(menu.name)}
                      className="w-full text-left p-3 hover:bg-gray-50 first:rounded-t-lg last:rounded-b-lg"
                    >
                      <span 
                        className="font-roboto text-[14px] font-medium"
                        style={{ color: colors.black1 }}
                      >
                        {menu.name}
                      </span>
                    </button>
                  ))
                ) : (
                  <div className="p-3">
                    <span 
                      className="font-roboto text-[14px] font-medium"
                      style={{ color: colors.black4 }}
                    >
                      No other menus available
                    </span>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>

        {/* Menu Title */}
        <div className="px-4 py-4">
          <h2 
            className="font-roboto text-[24px] font-semibold"
            style={{ color: colors.black1 }}
          >
            {selectedMenuName}
          </h2>
        </div>

        {/* Section Tabs */}
        <div className="px-4 pb-4">
          <div className="flex border-b border-neutral-200">
            {menuSections.map((section) => (
              <button
                key={section.id}
                onClick={() => setSelectedSection(section.id)}
                className={`flex-1 py-3 px-4 font-roboto text-[14px] font-medium border-b-2 transition-colors ${
                  selectedSection === section.id
                    ? 'border-black text-black'
                    : 'border-transparent text-gray-600 hover:text-black'
                }`}
              >
                {section.name}
              </button>
            ))}
          </div>
        </div>

        {/* Menu Items Section */}
        <div className="flex-1 px-4 pb-4 overflow-y-auto">
          {(() => {
            const currentSection = menuSections.find(section => section.id === selectedSection);
            
            if (!currentSection || currentSection.items.length === 0) {
              return (
                <div className="flex flex-col items-center justify-center py-16 text-center">
                  <div 
                    className="w-16 h-16 rounded-full mb-4 flex items-center justify-center"
                    style={{ backgroundColor: colors.black6 }}
                  >
                    <Icon path={mdiClose} size={1.5} color={colors.black4} />
                  </div>
                  <h3 
                    className="font-roboto text-[18px] font-semibold mb-2"
                    style={{ color: colors.black1 }}
                  >
                    No items in {currentSection?.name}
                  </h3>
                  <p 
                    className="font-roboto text-[14px] max-w-64"
                    style={{ color: colors.black4 }}
                  >
                    This section doesn't have any items yet. Check back later for delicious options!
                  </p>
                </div>
              );
            }

            return (
              <div className="space-y-4">
                {currentSection.items.map((item) => (
                  <div key={item.id} className="flex items-start gap-3 p-3 border border-neutral-200 rounded-lg">
                    {item.image && (
                      <img 
                        src={item.image} 
                        alt={item.name}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                    )}
                    <div className="flex-1">
                      <h4 
                        className="font-roboto text-[16px] font-semibold mb-1"
                        style={{ color: colors.black1 }}
                      >
                        {item.name}
                      </h4>
                      <p 
                        className="font-roboto text-[14px] mb-2"
                        style={{ color: colors.black4 }}
                      >
                        {item.description}
                      </p>
                      <span 
                        className="font-roboto text-[16px] font-semibold"
                        style={{ color: colors.black1 }}
                      >
                        ${item.price.toFixed(2)}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            );
          })()}
        </div>
      </div>

      {/* Click outside to close dropdown */}
      {isMenuDropdownOpen && (
        <div 
          className="fixed inset-0 z-5"
          onClick={() => setIsMenuDropdownOpen(false)}
        />
      )}
    </div>
  );
};